<template>
  <div>
    <div class="div" ref="cbfNavMenu">
      <h2></h2>
      <el-radio-group
        v-model="navMenu.config.collapse"
        style="margin-bottom: 20px;"
      >
        <el-radio-button :label="false"></el-radio-button>
        <el-radio-button :label="true"></el-radio-button>
      </el-radio-group>
      <!-- nav-menu  -->
      <Nav-menu
        ref="NavMenu"
        :config="navMenu.config"
        :content="navMenu.content"
        @HandleOpen="navMenuOpen"
        @HandleClose="navMenuClose"
      >
      </Nav-menu>
    </div>
    <div class="div">
      <h2>TABS</h2>
      <Tabs :config="tabs.config" :content="tabs.content">
        <slot>123</slot>
      </Tabs>
    </div>
    <div class="div">
      <h2></h2>
      <Breadcrumb
        :config="breadcrumb.config"
        :content="breadcrumb.content"
      ></Breadcrumb>
    </div>
    <div class="div">
      <h2></h2>
      <Page-header
        :config="pageHeader.config"
        :content="pageHeader.content"
      ></Page-header>
    </div>
    <div class="div">
      <h2>Messages</h2>
      <el-button @click="openInfo()">info</el-button>
      <el-button @click="openSuccess()">success</el-button>
      <el-button @click="openWarning()">warn</el-button>
      <el-button @click="openError()">error</el-button>
      <el-button @click="openHtml()">html</el-button>
    </div>
    <div class="div">
      <h2>Loading</h2>
      <el-button @click="startLoading()">
        3sloading
      </el-button>
    </div>
    <div class="div">
      <h2>dropdown</h2>
      <Dropdown :config="dropDown.config" :content="dropDown.content">
      </Dropdown>
    </div>
    <div class="div">
      <el-button @click="openMessageBox()">MessageBox</el-button>
    </div>
    <div class="div">
      <h2>Steps</h2>
      <Steps :config="steps.config" :content="steps.content"></Steps>
    </div>
    <div class="div">
      <h2>calendar</h2>
      <Calendar :config="calendar.config"> </Calendar>
    </div>
    <div class="div">
      <h2>timeline</h2>
      <Timeline>
        <timeline-item :content="timelineItem.content">
          <template v-slot:default="timelineItemSlots">
            {{ timelineItemSlots.timeLineItem.content }}
          </template>
        </timeline-item>
      </Timeline>
    </div>
    <div class="div">
      <Back-top :config="backtop.config">
        <el-button type="danger">
          
        </el-button>
      </Back-top>
    </div>
  </div>
</template>

<script>
import {
  Messages,
  Confirm,
  OpenLoading,
  CloseLoading
} from '../components/js/Notice';
import NavMenu from '../components/NavMenu';
import Tabs from '../components/Tabs';
import Breadcrumb from '../components/Breadcrumb';
import PageHeader from '../components/PageHeader';
import Dropdown from '../components/Dropdown';
import { NavMenuLists } from '../components/js/NavMenu';
import Steps from '../components/Steps';
import Calendar from '../components/Calendar';
import Timeline from '../components/Timeline';
import TimelineItem from '../components/TimelineItem';
import BackTop from '../components/BackTop';

export default {
  name: 'vue3',
  components: {
    NavMenu,
    Tabs,
    Breadcrumb,
    PageHeader,
    Dropdown,
    Steps,
    Calendar,
    Timeline,
    TimelineItem,
    BackTop
  },
  data() {
    return {
      popConfirm: {
        content: '123',
        config: {
          title: '',
          conText: '',
          canText: '',
          icon: 'el-icon-question',
          iconColor: 'red',
          hideIcon: false
        }
      },
      // NavMenu
      navMenu: {
        config: {
          mode: 'vertical',
          collapse: false
        },
        content: NavMenuLists
      },
      // Tabs
      tabs: {
        config: {
          activeName: '2',
          tabPosition: 'top',
          typeCard: 'border-card',
          subConfig: {}
        },
        content: [
          {
            title: 'Tab 1',
            name: '1',
            content: 'Tab 1 content'
          },
          {
            title: 'Tab 2',
            name: '2',
            content: 'Tab 2 content'
          }
        ]
      },
      // Breadcrumb
      breadcrumb: {
        content: [
          { name: '', url: 'www.baidu.com' },
          { name: '' },
          { name: '' },
          { name: '' }
        ],
        config: {
          subConfig: {}
        }
      },
      pageHeader: {
        config: {
          content: '',
          title: ''
        },
        content: []
      },
      dropDown: {
        config: {
          subConfig: {}
        },
        content: [
          { name: '', id: '1' },
          { name: '', id: '2' },
          { name: '', id: '3' },
          { name: '', id: '4' }
        ]
      },
      steps: {
        config: {
          active: 2,
          direction: 'horizontal',
          simple: true
        },
        content: [
          { title: '', icon: 'el-icon-edit' },
          { title: '', icon: 'el-icon-upload' },
          { title: '', icon: 'el-icon-picture' },
          { title: '', icon: 'el-icon-picture' }
        ]
      },
      calendar: {
        config: {
          value: new Date(),
          range: ['2022-03-28', '2022-05-01']
        }
      },
      timeLine: {
        reverse: true
      },
      timelineItem: {
        content: [
          {
            content: '',
            timestamp: '2018-04-12 20:46',
            size: 'large',
            type: 'primary',
            icon: 'el-icon-more'
          },
          {
            content: '',
            timestamp: '2019-05-03 21:46',
            color: '#0bbd87'
          },
          {
            content: '',
            timestamp: '2020-06-09 10:46',
            size: 'large'
          },
          {
            content: '',
            timestamp: '2022-04-15 05:46',
            color: '#f08025'
          }
        ]
      },
      backtop: {
        config: {
          target: this.$refs.cbfNavMenu,
          visibilityHeight: 600,
          bottom: 150,
          right: 100
        }
      }
    };
  },
  methods: {
    navMenuOpen() {
      console.log('eee111');
    },
    navMenuClose() {
      console.log('eee22');
    },
    openNavMenu() {
      // this.$refs.NavMenu.handleOpen();
    },
    openInfo() {
      Messages({
        type: 'info',
        message: '',
        showClose: true
      });
    },
    openSuccess() {
      Messages({ type: 'success', message: '' });
    },
    openWarning() {
      Messages({ type: 'warning', message: '' });
    },
    openError() {
      Messages({ type: 'error', message: '' });
    },
    openHtml() {
      Messages({
        message: `<h1> <i>HTML</i> </h1>
      <h1> <i>HTML</i> </h1>`,
        dangerouslyUseHTMLString: true
      });
    },
    openMessageBox() {
      let err = '';
      Confirm(err, '', {
        type: 'warning',
        center: false,
        showCancelButton: true,
        confirmButtonText: ''
      })
        .then(() => {
          console.log('');
        })
        .catch(() => {
          console.log('');
        });
    },
    startLoading() {
      OpenLoading();
      setTimeout(() => {
        CloseLoading();
      }, 3000);
    },
    PageHeader() {
      this.$refs.child.PageHeaderGoBack();
    }
  },
  mounted() {}
};
</script>

<style>
.div {
  margin: 50px 0;
}
</style>
